import React, { Component } from 'react';
import './App.css';

class MainCalc extends Component {
  state = {
    value: 7
  }

  render() { 
    return (
      <div>
        <div className='container'>
          <h2 className='header-calc'>DC Calculator</h2>
          <input type="text" id="input" className='input' autoFocus disabled/>
          <div className='btns'>
            <button className='btn' onClick={ this.a }>7</button>
            <button className='btn' onClick={ this.b }>8</button>
            <button className='btn' onClick={ this.c }>9</button>
            <button className='btn' onClick={ this.d } onContextMenuCapture={ this.dDel }>Del</button>
          </div>
          <div className='btns'>
            <button className='btn' onClick={ this.e }>4</button>
            <button className='btn' onClick={ this.f }>5</button>
            <button className='btn' onClick={ this.g }>6</button>
            <button className='btn' onClick={ this.h }>&divide;</button>
          </div>
          <div className='btns'>
            <button className='btn' onClick={ this.i }>1</button>
            <button className='btn' onClick={ this.j }>2</button>
            <button className='btn' onClick={ this.k }>3</button>
            <button className='btn' onClick={ this.l }>&times;</button>
          </div>
          <div className='btns'>
            <button className='btn' onClick={ this.m }>.</button>
            <button className='btn' onClick={ this.n }>0</button>
            <button className='btn' onClick={ this.o }>&minus;</button>
            <button className='btn' onClick={ this.p }>+</button>
          </div>
          <div className='btns'>
            <button className='btn' onClick={ this.q }>&pi;</button>
            <button className='btn' onClick={ this.r }>^</button>
            <button className='btn' onClick={ this.s }>%</button>
            <button className='btn' onClick={ this.t } onBlur={ this.tBlur }>=</button>
          </div>

          <h3 className='bottom'>&copy; 2022 | By Dave Conco&reg;</h3>
        </div>
      </div>
    );
  }

  a = () => {
    document.getElementById("input").value += 7;
  }
  b = () => {
    document.getElementById("input").value += 8;
  }
  
  c = () => {
    document.getElementById("input").value += 9;
  }
  d = () => {
    const val = document.getElementById("input").value;
    let res = val.replace(val.charAt(val.length-1), "");
    document.getElementById("input").value = res;
  }
  dDel = (e) => {
    e.preventDefault();
    document.getElementById("input").value = "";
  }
  e = () => {
    document.getElementById("input").value += 4;
  }
  f = () => {
    document.getElementById("input").value += 5;
  }
  g = () => {
    document.getElementById("input").value += 6;
  }
  h = () => {
    document.getElementById("input").value += "/";
  }
  i = () => {
    document.getElementById("input").value += 1;
  }
  j = () => {
    document.getElementById("input").value += 2;
  }
  k = () => {
    document.getElementById("input").value += 3;
  }
  l = () => {
    document.getElementById("input").value += "*";
  }
  m = () => {
    document.getElementById("input").value += ".";
  }
  n = () => {
    document.getElementById("input").value += 0;
  }
  o = () => {
    document.getElementById("input").value += "-";
  }
  p = () => {
    document.getElementById("input").value += "+";
  }
  q = () => {
    // document.getElementById("input");
    // val.value += 8;
  }
  r = () => {
    document.getElementById("input").value += "**";
  }
  s = () => {
    document.getElementById("input").value += "%";
  }
  t = () => {
    const val = document.getElementById("input");
    const res = eval(val.value);
    res === undefined ? val.value = "Math Error" : val.value = res;
  }
  tBlur = () => {
    const val = document.getElementById("input");
    if (val.value === "Math Error") {
      val.value = "";
    }
  }
}
 
export default MainCalc;